video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero-container1 {
  /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.hero-container1 > h1 {
  color: black;
  font-size: 80px;
  margin-top: -100px;
  margin-right: 30%;
}

.hero-container1 > p {
  margin-top: 8px;
  color: black;
  font-size: 32px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}



@media screen and (max-width: 960px) {
  .hero-container1 > h1 {
    font-size: 50px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container1 > h1 {
    font-size: 40px;
    margin-top: -100px;

  }

  .hero-container1 > p {
    font-size: 30px;
  }

 
}
